body {
  margin: 0;
  padding: 0;
  background-color: #eeeeee;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  max-width: 1128px;
  margin: 0 auto;
}

.add_firm {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 400px;
    padding: 20px;
    background-color: #ddd;
}

.add_firm label {
    margin: 10px 0;

}

.add_firm input,
.add_firm textarea {
    float: right;
}
.mainTable {
    padding: 20px;
    border: 2px solid black;
    margin: 0 auto;

}



.row {
    display: -webkit-flex;
    display: flex;
    background-color: #d2d2d2;
    margin: 10px 0;
    padding: 10px;
}

.row div:nth-child(1) {
    width: 20%;
}

.row div:nth-child(2) {
    width: 20%;
    overflow: hidden;
}

.row div:nth-child(3) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(4) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(5) {
    width: 10%;
    text-align: center;
}

.row div:nth-child(6) {
    width: 10%;
    text-align: center;
}

.row.title {
    font-weight: 700;
    text-align: center;
}
